.navbar{
    position: fixed; 
    top: 0; /*to allign in top*/
    width: 100%;
    height: 30px;
    background-color: #111;
    padding: 20px;
    display: flex;
    justify-content: space-between;

}
.logo{
    position: fixed; /*to fix in a side*/
    left: 20px; /*to fix in left*/
    width: 80px;
}
.avatar{
    position: fixed;
    right: 20px;
    width: 30px;
}
*{
    margin: 0; /*We are removing the default margin, padding using '  *  ' ,here we are importing
                 all components to App.js, so we giving this in main css file*/
    padding: 0;

}
body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #111;
}
.banner{
    
    background-size: cover;
    height: 448px;
    color: white; /*for every font in banner*/
}

.content{
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;
}

.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.button{
    color: white; /*font color*/
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51,51,0.5); /*opacity 0.5*/
    cursor: pointer;
    margin-right: 1rem;
}

.button:hover{
    color: black;
    background-color: #e6e6e6;
}

.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px; /*to set content width*/
}

.fade_bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37,37,37,0.61), #111);
}
.row{
    margin-left: 20px;
    color: #fff;
}

.posters{
    display: flex; /*to show row wise when multiple images importing*/
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;


}
.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    max-height: 250px;
    margin-right: 10px;
    cursor: pointer;
}

.smallPoster{
    max-height: 150px;
    margin-right: 10px;
    cursor: pointer;
}

.poster:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
